import React, { Component } from "react";
import SideNav from '../Partials/Sidenav'
import MainNav from "../Partials/Mainnav";
import ManagerSymptomTable from "./ManagerSymptomTable";
import * as ManagerSymptomAction from "../../Redux/Actions/ManagerSymptomAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import Tabs from "../Reusables/TabLayout/Tabs"
import '../Reusables/TabLayout/style.css';



class ManagerSymptom extends Component {
    constructor(props) {
        super(props);
        this.state = {
            description: ''
        }
    }

    render() {
        const { input, isLoading } = this.props;
        // console.log(input, "input check check");
        return (
            <div className='h-100'>
                <div className="container-fluid">
                    <div className="row">
                        <SideNav />
                        <main className="main-content col-lg-10 col-md-9 col-sm-12 p-0 offset-lg-2 offset-md-3">
                            <div className="main-navbar sticky-top bg-white">
                                <MainNav />
                            </div>
                            <div className="container-fluid mt-3">
                                <div className="row">
                                    <div className="col-md-12 col-sm-4">

                                    </div>
                                </div>
                            </div>


                            <div className="row container">
                                <div className="col-md-12 col-sm-4">

                                    <div className="card shadow p-4 rounded-5">
                                        <div className="page-header row no-gutters py-4">
                                            <div className="col-12 col-sm-4 text-center text-sm-left mb-0" style={{ paddingTop: 10, paddingBottom: 40 }}>
                                                <h2 style={{ color: "#323232" }} className="page-title">Manage Symptoms</h2></div></div>
                                        <Tabs>
                                            <div label="All symptom">
                                                <ManagerSymptomTable />
                                            </div>
                                            <div label="Add new">
                                                <div className="card shadow p-4 rounded-5">

                                                    <form onSubmit={(event) => this.props.manageSymptomAction.handleSumbit(event, input,this.state.description)}>
                                                        <div className="form-group col-md-4 mt-3">
                                                            <h6 style={{ color: "#323232", fontWeight: 'bold' }} className="page-title">New symptom</h6></div>
                                                        <div className="form-row">
                                                            <div className="form-group col-md-4 mt-3">
                                                                <input
                                                                    type="text"
                                                                    className="form-control rounded-0"
                                                                    id="inputName"
                                                                    placeholder="Symptom Name"
                                                                    value={input}
                                                                    required
                                                                    onChange={event => this.props.manageSymptomAction.onchangeHandle(event)}
                                                                />
                                                            </div>
                                                        </div>
                                                        <div className="form-row">
                                                            <div className=" col-md-4 form-group mt-4">
                                                                <textarea
                                                                    className="form-control rounded-0"
                                                                    id="exampleFormControlTextarea1"
                                                                    rows="3"
                                                                    placeholder="Description"
                                                                    value={this.state.description}
                                                                    onChange={event => this.setState({ description: event.target.value })}
                                                                ></textarea>
                                                            </div></div>

                                                        <div className="form-row">
                                                            <div className="form-group col-md-4 col-md-offset-4 mt-3">
                                                                <button
                                                                    type="sumbit"
                                                                    className="btn text-white rounded-0 w-100"
                                                                    style={{ backgroundColor: "#1B2CC1", height: "35px" }}
                                                                    disabled={isLoading}
                                                                >
                                                                    {isLoading ? "Loading.." : "Create"}
                                                                </button>

                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                            </div>

                                        </Tabs>
                                    </div>
                                </div></div>
                        </main>
                    </div>
                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    input: state.ManagerSymptomReducer.input,
    isLoading: state.LoaderReducer.isLoading

});
const mapDispatchToProps = (dispatch) => ({
    manageSymptomAction: bindActionCreators(ManagerSymptomAction, dispatch)

});

export default connect(mapStateToProps, mapDispatchToProps)(ManagerSymptom);