import React, { Component } from "react";
import { connect } from "react-redux";
import moment from "moment";
import { bindActionCreators } from "redux";
import * as ManagerSicknessAction from "../../Redux/Actions/MangerSicknessAction";
import DatatablePage from "../Reusables/DataTable/dataTables"
import swal from "sweetalert"
import EditSicknessModal from "./EditSicknessModal";

class MangerSicknessTable extends Component {

    componentDidMount() {
        this.props.manageSicknessAction.getAllSickness();
    }
    doNothing = () => {
        console.log("doing nothing");
    }

    toggleCheckBox = (id) => {
        var checkBoxValue = "";
        var $ = window.$;
        $('.teamgrace').each(function () {
            let checkBoxId = parseInt(this.id);
            let checkId = parseInt(id);


            $('#' + checkBoxId).prop('checked', function (i, val) {
                return val && checkBoxId === checkId ? true : false;
            });

        });
        $('.teamgrace').each(function () {
            let checkBoxId = parseInt(this.id);
            let checkId = parseInt(id);
            if (checkBoxId === checkId) {
                $('#' + checkId).prop('checked', function (i, val) {
                    return val ? false : true;
                });

                checkBoxValue = $(this).prop('checked');


            }
        });
        if (checkBoxValue) {
            this.updateSickness(id);
        } else {
            this.props.manageSymptomAction.updateSicknessFinally(null);
        }


    }
    updateSickness(id) {
        const { sickness} = this.props;
        let sicknessObj = sickness.find(symp => parseInt(symp.id) === parseInt(id));
        this.props.manageSicknessAction.updateSicknessFinally(sicknessObj)
    }

    checkActionToPerform = () => {
        var $ = window.$;
        let checkBoxValue = false;
        $('.teamgrace').each(function () {
            checkBoxValue =  $(this).prop('checked'); 
            if (checkBoxValue) {
                return false;
            }
        });
        if (!checkBoxValue) {
            swal("Select a sickness!");
            return;
        }


        let sicknessSelect = $('#actionDropDown').val();
        if (sicknessSelect === "") {
            swal("Select an Action!");
            return;
        }
        if (sicknessSelect === "delete") {
            swal({
                title: "Are you sure?",
                text: "Once deleted, you will not be able to recover this again!",
                icon: "warning",
                buttons: true,
                dangerMode: true,
            })
                .then((willDelete) => {
                    if(willDelete){
                    this.props.manageSicknessAction.deleteSicknessFinally((responseCallBack)=>{
                        if(responseCallBack){
                            swal("Symptom deleted successfully!", {
                                icon: "success",
                            });
                        }
                    });}
                   
                });
        }
        if(sicknessSelect==="edit"){
          $("#sicknessModal").modal('show');
        }
    }

    getSicknes = () => {
        let allSickness = [];
        const { sickness } = this.props;
        sickness.map((sickness, index) => {
            // console.log(product, "full product");
            return allSickness.push({
                serial: index + 1,
                sicknessName: sickness.sicknessName,
                check: (
                    <div
                        className="custom-control custom-checkbox mb-1"
                        onClick={() => this.toggleCheckBox(sickness.id)} >
                        <input
                            type="checkbox"
                            className="custom-control-input teamgrace"
                            id={sickness.id}
                            checked={false} onChange={() => this.doNothing()}
                        />
                        <label className="custom-control-label" htmlFor="category2"></label>
                    </div>
                    // <CheckBox type={'checkbox'} checked={this.state.item === symptomObject.id} label={''} onClick={() => this.toggleCheckBox(symptomObject.id)} />
                ),

         
                sicknessDescription:sickness.sicknessDescription?sickness.sicknessDescription:"NIL",
                dateTimeCreated: moment(sickness.dateTimeCreated).format("lll"),
                
            });
        });
        const data = {
            columns: [
                {
                    label: "S/N",
                    field: "serial",
                    sort: "asc"
                },
                {
                    label: 'Check',
                    field: 'check',
                    sort: 'disabled',
                },
                {
                    label: "Sickenss Name",
                    field: "sicknessName"
                },
                {
                    label: "Sickenss Description",
                    field: "sicknessDescription"
                },
                {
                    label: "Date|Time|Created",
                    field: "dateTimeCreated"
                }
            ],
            rows: allSickness
        };
   return data;
    }


    render() {

        return (
            <div className="container-fluid mt-4 mb-3">
            <div className="row">
                <div className="col-md-10 col-sm-12" >
                <div className="row">
                            <div className="form-group col-md-2 col-sm-2">
                                <select id="actionDropDown" className="form-control">
                                    <option value="" defaultValue>Choose...</option>
                                    <option value="edit">Edit</option>
                                    <option value="delete">Delete</option>
                                </select>
                            </div>
                            <div className="form-group col-md-2 col-sm-2">

                                <button
                                    type="button"
                                    onClick={() => this.checkActionToPerform()}
                                    className="btn text-white "
                                    style={{ backgroundColor: "#1B2CC1", height: "35px", borderRadius: '5px' }}

                                >
                                    Apply
                                </button>

                            </div>


                        </div>
                        <DatatablePage
                        data={this.getSicknes()}
                        searching={true}/>
                    </div>
               <EditSicknessModal/>
            </div>
        </div>
        )
    }
}


const mapStateToProps = (state) => ({
    sickness: state.ManagerSicknessReducer.sickness

});
const mapDispatchToProps = (dispatch) => ({
    manageSicknessAction: bindActionCreators(ManagerSicknessAction,dispatch)
});


export default connect(mapStateToProps,mapDispatchToProps)(MangerSicknessTable);