import { combineReducers } from 'redux'
import NavReducer from './nav'
import ManagerSymptomReducer from "./ManagerSymptomReducer";
import ManagerSicknessReducer from "./MangerSicknessReducer";
import SicknessSymptomReducer from "./SicknessSymptomReducer";
import LoaderReducer from "./LoaderReducer";
import AuthReducer from "./AuthReducer";

export default combineReducers({
    NavLinks: NavReducer,
    ManagerSymptomReducer,
    ManagerSicknessReducer,
    SicknessSymptomReducer,
    LoaderReducer,
    AuthReducer
})