import React, { Component } from "react";
import SideNav from '../Partials/Sidenav'
import MainNav from '../Partials/Mainnav'
import SicknessSymptomTable from "./SicknessSymptomTable";
import { connect } from "react-redux";
import * as SicknessSymptomAction from "../../Redux/Actions/SicknessSymptomAction";
import * as MangerSicknesAction from "../../Redux/Actions/MangerSicknessAction";
import * as ManagerSymptomAction from "../../Redux/Actions/ManagerSymptomAction";
import { bindActionCreators } from "redux";
import Tabs from "../Reusables/TabLayout/Tabs"
import '../Reusables/TabLayout/style.css';





class SicknessSymptom extends Component {
    constructor() {
        super();
        this.state = {
            sendingString: ""
        }
    }
    getAllSickness = () => {
        const { sickness } = this.props;
        console.log(sickness, "getAllsickness function")
        return (
            sickness.map((item) => (
                <option value={item.id} key={item.id}>{item.sicknessName}</option>
            ))
        )
    }

    getAllSymptoms = () => {
        const { symptoms } = this.props;
        console.log(symptoms, "getAllSymptoms function")
        return (
            symptoms.map((item, i) => (
                <option value={JSON.stringify({ id: item.id, symptomsName: item.symptomsName})} key={i}>{item.symptomsName}</option>
            ))
        )
    }

    componentDidMount() {
        const { symptoms, sickness } = this.props;
        if (!symptoms || symptoms.length == 0) this.props.manageSymptom.getAllSymptoms();
        if (!sickness || sickness.length == 0) this.props.manageSickness.getAllSickness();
    }

    renderAreaItems = (area) => {
        console.log({ area }, "area")
        let textAreaValue = [];
        if (Array.isArray(area)) {
            // i changed area.map to area.forEach in case error in feature.
            area.forEach(item => { textAreaValue.push(item.symptomsName) })
        }
        console.log(textAreaValue, 'i am the string')
        return textAreaValue && textAreaValue.length>0?textAreaValue.toString():"";
    }

    render() {
        const { sicknessInput, symptomsInput, area, isLoading } = this.props;

        return (
            <div className='h-100'>
                <div className="container-fluid">
                    <div className="row">
                        <SideNav />
                        <main className="main-content col-lg-10 col-md-9 col-sm-12 p-0 offset-lg-2 offset-md-3">
                            <div className="main-navbar sticky-top bg-white">
                                <MainNav />
                            </div>
                            <div className="container-fluid mt-3">
                                <div className="row">
                                    <div className="col-md-12 col-sm-4">

                                    </div>
                                </div>
                            </div>
                            <div className="row container">
                                <div className="col-md-12 col-sm-4">

                                    <div className="card shadow p-4 rounded-5">
                                        <div className="page-header row no-gutters py-4">
                                            <div className="col-12 col-sm-12 text-center text-sm-left mb-0" style={{ paddingTop: 10, paddingBottom: 40 }}>
                                                <h2 style={{ color: "#323232" }} className="page-title">Manage Sickness-Syptoms</h2></div></div>
                                        <Tabs>
                                            <div label="All sickness-symptoms">
                                                <SicknessSymptomTable />
                                            </div>
                                            <div label="Add new">
                                                <div className="card shadow p-4 rounded-5" >
                                                    <form onSubmit={(event) => this.props.manageSicknesSymptom.submitHandle(event, sicknessInput, symptomsInput)} >
                                                        <div className="form-group col-md-4 mt-3">
                                                            <h6 style={{ color: "#323232", fontWeight: 'bold' }} className="page-title">New sickness-symtpoms</h6></div>

                                                        <div className="form-row">
                                                            <div className="form-group col-lg-4 col-md-6 mt-4">
                                                                <select required className="form-control rounded-0" id="exampleFormControlSelect1" onChange={event => this.props.manageSicknesSymptom.sicknessInputHandler(event)}>
                                                                    <option value="" >Select sickness</option>
                                                                    {this.getAllSickness()}
                                                                </select>
                                                            </div>
                                                        </div>
                                                        <div className="form-row">
                                                            <div className="form-group col-lg-4 col-md-6 mt-4">
                                                                <div className="form-group">
                                                                    <select className="form-control rounded-0" id="exampleFormControlSelect1" onChange={event => this.props.manageSicknesSymptom.symptomInputHandler(event)}>
                                                                        <option >Select symptom</option>
                                                                        {this.getAllSymptoms()}
                                                                        {/* {symptoms.map((item, i) => (
                                                                            <option value={JSON.stringify({ id: i, name: item })} key={i}>{item}</option>
                                                                        ))} */}
                                                                    </select>
                                                                </div>
                                                            </div>
                                                        </div>
                                                        <div className="form-row">
                                                            <div className="form-group col-lg-4 col-md-6 mt-0">
                                                                <textarea
                                                                    required
                                                                    className="form-control rounded-0"
                                                                    id="exampleFormControlTextarea1"
                                                                    rows="4"
                                                                    value={this.renderAreaItems(area)}
                                                                    style={{fontSize:18}}
                                                                >
                                                                </textarea>
                                                            </div>

                                                        </div>
                                                        <div className="form-row">
                                                            <div className="form-group col-lg-4 col-md-4" style={{ marginTop: "37px" }}>
                                                                <button
                                                                    type="sumbit"
                                                                    className="btn text-white rounded-0 w-100"
                                                                    style={{ backgroundColor: "#1B2CC1", height: "35px" }}>
                                                                    {isLoading ? "Loading.." : "Create"}
                                                                </button>
                                                            </div>
                                                        </div>
                                                    </form>
                                                </div>
                                            </div>

                                        </Tabs>
                                    </div>
                                </div></div>

                        </main>
                    </div>
                </div>
            </div >
        )
    }
}

const mapStateToProps = (state) => ({
    sicknessInput: state.SicknessSymptomReducer.sicknessInput,
    symptomsInput: state.SicknessSymptomReducer.symptomsInput,
    area: state.SicknessSymptomReducer.area,
    symptoms: state.ManagerSymptomReducer.symptom,
    sickness: state.ManagerSicknessReducer.sickness,
    isLoading: state.LoaderReducer.isLoading

})
const mapDispatchToProps = (dispatch) => ({
    manageSicknesSymptom: bindActionCreators(SicknessSymptomAction, dispatch),
    manageSickness: bindActionCreators(MangerSicknesAction, dispatch),
    manageSymptom: bindActionCreators(ManagerSymptomAction, dispatch)

});

export default connect(mapStateToProps, mapDispatchToProps)(SicknessSymptom);