import { SHOW_SICKNESS_SYMPTOMS, SICKNESS_INPUT_HANDLER, SYMPTOM_INPUT_HANDLER, GET_SICKNESS, GET_SYMPTOMS } from "../Actions/Types";

const initialState = {
    sicknessSymptom: [],
    area: [],
    sicknessInput: "",
    symptomsInput: []
}

const SicknessSymptomReducer = (state = initialState, action) => {
    switch (action.type) {

        case SHOW_SICKNESS_SYMPTOMS:
            return { ...state, sicknessSymptom: action.payload }

        case SICKNESS_INPUT_HANDLER:
            return { ...state, sicknessInput: action.payload }

        case SYMPTOM_INPUT_HANDLER:
            if (!action.payload) {
                console.log("===========reseting input sickness========",action.payload);
                return { ...state, area: [],symptomsInput:[] }
            }
            let value = JSON.parse(action.payload);
            if (state.area.length === 0) {
                return { ...state, area: state.area.concat(value), symptomsInput: state.symptomsInput.concat(value.id) }
            } else {
                let items = state.area;
                let exist = items.filter((item) => item.id === value.id)
                if (exist.length === 0) {
                    return { ...state, area: state.area.concat(value), symptomsInput: state.symptomsInput.concat(value.id) }
                }
            }

            return { ...state }

        case GET_SICKNESS:
            return { ...state, sickness: action.payload }

        case GET_SYMPTOMS:
            return { ...state, symptoms: action.payload }

        default:
            return state;
    }
}

export default SicknessSymptomReducer;