import store from '../../Redux/store';
import { LOGIN_USER} from "../../Redux/Actions/Types";
import moment from 'moment';

const getLoggedInUserFromReducer = () => {
    return store.getState().AuthReducer.user;
}

const getLoggedInUserFromLocalStorage = () => {
    let user = localStorage.getItem("user");
    return user?JSON.parse(user):user;
}
const checkIfUserIsActive=()=>{
  let user =getLoggedInUserFromLocalStorage();
  var currrentDate = new Date();
  if(user){
    const expDate = new Date(user.expiresIn);
    if(currrentDate.getTime()>expDate.getTime()){
        return false
    }
    store.dispatch({ type: LOGIN_USER, payLoad: user});
    return true;
  }
  return false;
}

const saveLoggedInUserToLocalStorage = (key, payLoad) => {
    localStorage.setItem(key, JSON.stringify(payLoad));
}
const removeFromLocalStorage = (key) => {
    localStorage.removeItem(key)
}
export {
    getLoggedInUserFromReducer, getLoggedInUserFromLocalStorage, saveLoggedInUserToLocalStorage, removeFromLocalStorage,checkIfUserIsActive
}