import { SHOW_CREATED_SYMPTOMS, ONCHANGE_HANDLE, START_LOADING, STOP_LOADING, UPDATE_SYMPTOM } from "./Types";
import { settings, showErrorMessage, showSuccessfulMessage } from "../../../src/components/Utils/Utility"
import axios from "../../components/Utils/Clients";
import store from "../store";

//SHOW_CREATED_SYMPTOM table action type.
export const getAllSymptoms = () => {
    return dispatch => {
        axios.get(settings.baseURL + "symptoms")
            .then(response => {
                dispatch({
                    type: SHOW_CREATED_SYMPTOMS,
                    payload: response.data.data
                })
            })
            .catch(error => {
                console.log(error)
            })
    }
}

//ONCHANGE_NEW_SYMPTOM action type.
export const onchangeHandle = (event) => {
    let newValue = event.target.value;
    return dispatch => {
        dispatch({
            type: ONCHANGE_HANDLE,
            payload: newValue
        })
    }
}

export const updateSymptomFinally = (payload) => {
    return dispatch => {
        dispatch({
            type: UPDATE_SYMPTOM,
            payload
        })
    }
}

//handleSumbit button.
export const handleSumbit = (event, input,description) => {
    return dispatch => {
        console.log(input, 'i am the event')
        event.preventDefault();
        let payLoad = {
            symptomsName: input,
            description
        }
        dispatch({ type: START_LOADING })
        axios.post(settings.baseURL + "symptoms", payLoad)
            .then(response => {
                showSuccessfulMessage("Symptom created successfully")
                dispatch(getAllSymptoms())
                console.log(response, "i am the post response");
                dispatch({ type: STOP_LOADING })
                dispatch({
                    type: ONCHANGE_HANDLE, payload: ""
                })
            })
            .catch(error => {
                if (error.response && error.response.status === 409) {
                    showErrorMessage(error.response.data.message);
                } else {
                    showErrorMessage("Unable to process");
                }
                console.log({ error }, "i am the post error");
                dispatch({ type: STOP_LOADING })
            })
    }

}


export const updateSymptom = (payLoad,responseCallBack) => {
    return dispatch => {
        console.log("====paylOAD=====", payLoad);
        dispatch({ type: START_LOADING })
        axios.put(settings.baseURL + "symptoms", payLoad)
            .then(response => {
                showSuccessfulMessage("Symptom updated successfully")
                dispatch(getAllSymptoms())
                dispatch({ type: STOP_LOADING });
                responseCallBack(true);
            })
            .catch(error => {
                if (error.response && error.response.status === 409) {
                    showErrorMessage(error.response.data.message);
                } else {
                    showErrorMessage("Unable to process");
                }
                console.log({ error }, "i am the post error");
                dispatch({ type: STOP_LOADING })
            })
    }}

    export const deleteSymptomFinally = (responseCallBack) => {
        return (dispatch,getState)=> {
            let payLoad =store.getState().ManagerSymptomReducer.symptomObject;
            dispatch({ type: START_LOADING })
            axios.delete(settings.baseURL + "symptoms/"+payLoad.id)
                .then(response => {
                    dispatch(getAllSymptoms())
                    dispatch({ type: STOP_LOADING });
                    responseCallBack(true);
                })
                .catch(error => {
                    if (error.response && error.response.status === 409) {
                        showErrorMessage(error.response.data.message);
                    } else {
                        showErrorMessage("Unable to process");
                    }
                    console.log({ error }, "i am the post error");
                    dispatch({ type: STOP_LOADING })
                })
        }

}
