import React from 'react'
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import login from "./components/Login/Login";
import dashboard from "./components/Dashboard/dashboard";
import mangersickness from "./components/MangerSickness/MangerSickness";
import managersymptom from "./components/ManagerSymptom/ManagerSymptom";
import sicknesssymptom from "./components/SicknessSymptom/SicknessSymptom";
import {PrivateRoute} from "./components/Utils/PrivateRoute"

const Router = () => {
    return (
        <BrowserRouter>
            <Switch>
                <Route exact path='/login' component={login} />
                <PrivateRoute exact path="/" component={dashboard}/>
                <PrivateRoute exact path="/mangersickness" component={mangersickness}/>
                <PrivateRoute exact path="/managersymptom" component={managersymptom}/>
                <PrivateRoute exact path="/sicknesssymptom" component={sicknesssymptom}/>
            </Switch>
        </BrowserRouter>
    )
}

export default Router