import { SHOW_SICKNESS_SYMPTOMS, SICKNESS_INPUT_HANDLER, SYMPTOM_INPUT_HANDLER, GET_SICKNESS, GET_SYMPTOMS } from "./Types";
import axios from "../../components/Utils/Clients";
import { settings, showErrorMessage, showSuccessfulMessage } from "../../../src/components/Utils/Utility"

//SICKNESS_SYMPTOM_ACTION.
export const getSymptomSickness = () => {
    return dispatch => {
        axios.get(settings.baseURL + "symptoms-sickness")
            .then(response => {
                dispatch({
                    type: SHOW_SICKNESS_SYMPTOMS,
                    payload: response.data.data
                })
            })
            .catch(error => {
                console.log(error, " i am  error");
            })
    }
}

//SICKNESS_INPUT_HANDLER.
export const sicknessInputHandler = (event) => {
    let newvalue = event.target.value;
    return dispatch => {
        dispatch({
            type: SICKNESS_INPUT_HANDLER,
            payload: newvalue
        })
    }
}

//SYMPTOM_INPUT_HANDLER.
export const symptomInputHandler = (event) => {
    let value = event.target.value;
    return dispatch => {
        dispatch({
            type: SYMPTOM_INPUT_HANDLER,
            payload: value
        })
    }
}

//SUBMITHANDLE.
export const submitHandle = (event, sicknessInput, symptomsInput) => {
    return dispatch => {

        event.preventDefault();
        let payLoad = {
            sicknessId: sicknessInput,
            symptoms: symptomsInput.join(",")
        }
        axios.post(settings.baseURL + "symptoms-sickness", payLoad)
            .then(response => {
                showSuccessfulMessage("Created successfully");
                dispatch(getSymptomSickness())
                dispatch({type: SYMPTOM_INPUT_HANDLER,  payload: ""})
                dispatch({type: SICKNESS_INPUT_HANDLER,  payload: ""})
            })
            .catch(error => {
                if (error.response && error.response.status == 409) {
                    showErrorMessage(error.response.data.message);
                } else {
                    showErrorMessage("Unable to process!");
                }
                console.log({ error }, " i am the post error")
            })
    }
}


