import { CHANGE_ACTIVE } from '../Actions/Types'

const initialState = {
    active: false,
    id: 1,
    navList: [
        {
            id: 1,
            title: 'Dashboard',
            link: '',
            icon: '/images/dashboard.png'
        },
        {
            id: 2,
            title: 'Manage Sickness',
            link: 'mangersickness',
            icon: '/images/sicknessImage.png',
        },
        {
            id: 3,
            title: 'Manage Symptoms',
            link: 'managersymptom',
            icon: '/images/symptomImage.png',
        },
        {
            id: 4,
            title: 'Sickness Symptoms',
            link: 'sicknesssymptom',
            icon: '/images/sicknessSymptomImage.png',
        },
        {
            id: 5,
            title: 'Logout',
            link: '',
            icon: '/images/logOut.png'
        }
    ]
}

const NavReducer = (state = initialState, action) => {
    if (action.type === CHANGE_ACTIVE) {
        let active = true
        let id = action.id
        return {
            ...state,
            active,
            id
        }
    }
    return state
}

export default NavReducer