import React, { Component } from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import moment from "moment";
import * as SicknessSymptomAction from "../../Redux/Actions/SicknessSymptomAction";
import DatatablePage from "../Reusables/DataTable/dataTables"

class MangerSicknessTable extends Component {

    componentDidMount() {
        this.props.manageSicknesSymptom.getSymptomSickness();
    }

    getAllSymptoms=(symptoms)=>{
        return symptoms.join(',');

    }

    getSicknesSymptom = () => {
        let allSicknessSymptom = [];
        const { sicknessSymptom } = this.props;
        sicknessSymptom.map((sicknessSymptom, index) => {
            return allSicknessSymptom.push({
                serial: index + 1,
                sicknessName: sicknessSymptom.sickness,
                symptoms: this.getAllSymptoms(sicknessSymptom.symptoms),
                dateTimeCreated: moment(sicknessSymptom.dateTimeCreated).format("lll")
            });
        });
        const data = {
            columns: [
                {
                    label: "S/N",
                    field: "serial",
                    sort: "asc"
                },
                {
                    label: "Sickenss Name",
                    field: "sicknessName"
                },
                {
                    label: "Symptoms",
                    field: "symptoms"
                },
                {
                    label: "Date|Time|Created",
                    field: "dateTimeCreated"
                }
            ],
            rows: allSicknessSymptom
        };
   return data;
    }

    render() {

        return (
            <div className="container-fluid mt-4 mb-3">
            <div className="row">
                <div className="col-md-12 col-sm-12" >
                        <DatatablePage
                        data={this.getSicknesSymptom()}
                        searching={true}/>
                    </div>
               
            </div>
        </div>
        )
    }
}

const mapStateToProps = (state) => {
    const { sicknessSymptom } = state.SicknessSymptomReducer;
    return {
        sicknessSymptom
    }
}
const mapDispatchToProps = (dispatch) => ({
    manageSicknesSymptom: bindActionCreators(SicknessSymptomAction, dispatch)

});

export default connect(mapStateToProps,mapDispatchToProps)(MangerSicknessTable);