import { SHOW_CREATED_SYMPTOMS, ONCHANGE_HANDLE,UPDATE_SYMPTOM} from "../Actions/Types";

const initialState = {
    symptom: [],
    input: "",
    symptomObject:null
}

const ManagerSymptomReducer = (state = initialState, action) => {
    switch (action.type) {
        case SHOW_CREATED_SYMPTOMS:
            return { ...state, symptom: action.payload };

        case ONCHANGE_HANDLE:
            return { ...state, input: action.payload }

            case UPDATE_SYMPTOM:
                return { ...state, symptomObject: action.payload }
    

        default:
            return state;
    }
}

export default ManagerSymptomReducer;