import React, { Component } from "react";
import { Link } from "react-router-dom";
import * as LoginAction from "../../Redux/Actions/AuthAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";



class Login extends Component {

    constructor(props) {
        super(props);
        this.state = {
            userName: "",
            password: ""
        };
    }

    handleChange = (e) => {
        const { name, value } = e.target;
        this.setState({
            [name]: value
        });
    }
    handleSubmit = (e) => {
        e.preventDefault();
        this.props.loginAction.loginUser(this.state, this.props);
    }


    render() {
        const { isLoading } = this.props;
        return (
            <div className="container-fluid" style={{ marginTop: "200px" }}>

                <div className="row">
                    {/*The col that give it space by the rightside */}
                    <div className="col-md-4 col-sm-4"></div>
                    {/* The end of the col that give it space by the rightside */}

                    <div className="col-md-4 col-sm-4">

                        <div className="card p-4 bg-white rounded-5" >
                            {/* <div  style={{margin:20,marginTop:0}}>
                                <Link to='/' className="navbar-brand w-100 mr-0" style={{ lineHeight: '25px' }}>
                                    <div className="d-table m-auto">
                                        <img id="main-logo" className="d-inline-block align-top mr-1" style={{ maxWidth: '30px' }}
                                            src={require("../../assets/images/logo.png")}
                                            alt="Shards Dashboard"
                                        />
                                        <span className="d-none d-md-inline ml-1" style={{ fontSize: 50, color: "#1B2CC1" }}>aegle</span>
                                    </div>
                                </Link>
                                <Link to='/' className="toggle-sidebar d-sm-inline d-md-none d-lg-none">
                                    <i className="material-icons"></i>
                                </Link>
                            </div> */}


                            <h2
                                className="card-title text-center"
                                style={{ color: "#111", fontSize: "30px", fontFamily: "Roboto,  sans-serif" }}
                            >
                                <b>LOGIN</b>
                            </h2>
                            <form id="loginForm" onSubmit={this.handleSubmit} style={{padding:20}}>
                                <div className="form-row">
                                </div>
                                <div className="form-group mt-2" style={{padding:10}}>
                                    <input
                                        type="text"
                                        className="form-control rounded-0"
                                        name="userName"
                                        placeholder="Username"
                                        value={this.state.userName}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                                <div className="form-group mt-3" style={{padding:10}}>
                                    <input
                                        type="password"
                                        className="form-control rounded-0"
                                        name="password"
                                        placeholder="Password"
                                        value={this.state.password}
                                        onChange={this.handleChange}
                                        required
                                    />
                                </div>
                                <button
                                    type="sumbit"
                                    className="btn text-white rounded-0 w-100"
                                    style={{ backgroundColor: "#1B2CC1", height: "35px" }}
                                    disabled={isLoading}>
                                    {isLoading ? "Loading.." : "LOGIN"}
                                </button>
                            </form>
                        </div>
                    </div>

                    {/*The col that give it space by the leftside */}
                    <div className="col-md-4 col-sm-4"></div>
                    {/* The end of the col that give it space by the leftside */}

                </div>
            </div>
        )
    }
}

const mapStateToProps = (state) => ({
    isLoading: state.LoaderReducer.isLoading
});

const mapDispatchToProps = (dispatch) => ({
    loginAction: bindActionCreators(LoginAction, dispatch)

});
export default connect(mapStateToProps, mapDispatchToProps)(Login);