
const settings = {
        baseURL: 'https://teamgracepredict.com/api/',
     // baseURL: 'http://localhost:8083/api/'
}
 const showSuccessfulMessage=(msg) =>{
    var $ = window.$;
    $.toast({
        heading: "Successful",
        text: msg,
        showHideTransition: "slide",
        icon: "success",
        position: "top-right",
        hideAfter: 3000
    });
}
 const showErrorMessage=(msg) =>{
    var $ = window.$;
    $.toast({
        heading: "Error",
        text: msg,
        showHideTransition: "slide",
        icon: "error",
        position: "top-right",
        hideAfter: 5000
    });
}

const roles ={
    ADMIN:"ADMIN",
    USER:"USER"
}
export { settings,showSuccessfulMessage,showErrorMessage,roles}     
