import React, { Component } from 'react'
import ManagerSymptomTable from "./ManagerSymptomTable";
import * as ManagerSymptomAction from "../../Redux/Actions/ManagerSymptomAction";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import swal from 'sweetalert';

class EditSymptomModal extends Component {

    constructor(props) {
        super(props);
        this.state = {
            showModal: false,
            symptomsName: '',
            description: '',
            id: ''
        }
    }

   
    componentWillReceiveProps(nextProps) {
        const { symptomObject } = nextProps;
        symptomObject && this.setState({
            symptomsName: symptomObject.symptomsName,
            description: symptomObject.description?symptomObject.description:"",
            id: symptomObject.id
        })

    }

    
    handleSubmit=(e)=>{
        var $ = window.$;
        e.preventDefault();
        this.props.manageSymptomAction.updateSymptom(this.state,(responseCallBack)=>{
         if(responseCallBack){
            $("#exampleModalLabel").modal('hide');
         }
        });

    }


    render() {
        const { isLoading,symptomObject } = this.props;
        
       
        return (
            <div className="col-md-12">
                <div
                    aria-labelledby="exampleModalLabel"
                    className="modal fade show"
                    id="exampleModalLabel"
                    role="dialog"
                    // tabIndex="-1"
                    style={{
                        display: "none",
                        paddingRight: "17px",
                        background: "transparent"
                    }}
                >
                    <div className="modal-dialog " role="document" style={{ flexDirection: 'row', flex: 1 }}>
                        <div className="modal-content text-center">
                            <div className="modal-header" style={{ flex: 1, flexDirection: 'row', justifyContent: 'center', alignItems: "center" }}>
                                <h6 style={{ color: "#323232", fontWeight: 'bold' }} className="page-title">Edit symptom</h6>
                                {/* <button aria-label="Close" className="close" data-dismiss="modal" type="button"><span className="close-label"></span><span className="os-icon os-icon-close"></span></button> */}
                            </div>
                            <div className="modal-body col-md-12" >
                                <div className="card shadow p-4 rounded-5">

                                    <form onSubmit={this.handleSubmit}>
                                        <div style={{ flex: 1, alignSelf: 'center', flexDirection: 'column', padding: 20 }}>
                                            <div>
                                                <div className="form-row">
                                                    <div className="form-group col-sm-11  col-md-11">
                                                        <input
                                                            type="text"
                                                            className="form-control rounded-0"
                                                            id="inputName"
                                                            placeholder="Symptom Name"
                                                            value={this.state.symptomsName}
                                                            onChange={(e)=>this.setState({symptomsName:e.target.value})}
                                                            name="symptomsName"
                                                            required
                                                        />
                                                    </div>
                                                </div>
                                                <div className="form-row">
                                                    <div className=" col-md-11  col-sm-12 form-group mt-4">
                                                        <textarea
                                                            className="form-control rounded-0"
                                                            id="exampleFormControlTextarea1"
                                                            rows="3"
                                                            placeholder="Description"
                                                            onChange={(e)=>this.setState({description:e.target.value})}
                                                            value={this.state.description}
                                                            name="description"

                                                        ></textarea>
                                                    </div>
                                                </div>

                                                <div className="form-row">
                                                    <div className="form-group col-md-11 ">
                                                        <button
                                                            type="sumbit"
                                                            className="btn text-white rounded-0 w-100"
                                                            style={{ backgroundColor: "#1B2CC1", height: "35px" }}
                                                            disabled={isLoading}
                                                        >
                                                            {isLoading ? "Loading.." : "Update"}
                                                        </button>

                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </form>

                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>)

    }
}

const mapStateToProps = (state) => ({
    isLoading: state.LoaderReducer.isLoading,
    symptomObject:state.ManagerSymptomReducer.symptomObject

});
const mapDispatchToProps = (dispatch) => ({
    manageSymptomAction: bindActionCreators(ManagerSymptomAction, dispatch)

});

export default connect(mapStateToProps, mapDispatchToProps)(EditSymptomModal);
