import React, { Component } from "react";
import moment from "moment";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import * as ManagerSymptomAction from "../../Redux/Actions/ManagerSymptomAction";
import DatatablePage from "../Reusables/DataTable/dataTables"
import { CheckBox } from '../Reusables';
import Modal from "react-bootstrap/Modal";
import EditSymptomModal from "./EditSymptomModal";
import swal from 'sweetalert';


class ManagerSymptomTable extends Component {

    constructor(props) {
        super(props);
        this.state = {
            item: ''
        }
    }


    doNothing = () => {
        console.log("doing nothing");
    }

    toggleCheckBox = (id) => {
        var checkBoxValue = "";
        var $ = window.$;
        $('.teamgrace').each(function () {
            let checkBoxId = parseInt(this.id);
            let checkId = parseInt(id);


            $('#' + checkBoxId).prop('checked', function (i, val) {
                return val && checkBoxId === checkId ? true : false;
            });

        });
        $('.teamgrace').each(function () {
            let checkBoxId = parseInt(this.id);
            let checkId = parseInt(id);
            if (checkBoxId === checkId) {
                $('#' + checkId).prop('checked', function (i, val) {
                    return val ? false : true;
                });

                checkBoxValue = $(this).prop('checked');


            }
        });
        if (checkBoxValue) {
            this.updateSymptom(id);
        } else {
            this.props.manageSymptomAction.updateSymptomFinally(null);
        }


    }
    checkActionToPerform = () => {
        var $ = window.$;
        let checkBoxValue = false;
        $('.teamgrace').each(function () {
            checkBoxValue =  $(this).prop('checked'); 
            if (checkBoxValue) {
                return false;
            }
        });
        if (!checkBoxValue) {
            swal("Select a symptom!");
            return;
        }


        let symptomSelect = $('#actionDropDown').val();
        if (symptomSelect === "") {
            swal("Select an Action!");
            return;
        }
        if (symptomSelect === "delete") {
            swal({
                title: "Are you sure?",
                text: "Once deleted, you will not be able to recover this again!",
                icon: "warning",
                buttons: true,
                dangerMode: true,
            })
                .then((willDelete) => {
                    if(willDelete){
                    this.props.manageSymptomAction.deleteSymptomFinally((responseCallBack)=>{
                        if(responseCallBack){
                            swal("Symptom deleted successfully!", {
                                icon: "success",
                            });
                        }
                    });}
                   
                });
        }
        if(symptomSelect==="edit"){
          $("#exampleModalLabel").modal('show');
        }
    }

    updateSymptom(id) {
        const { symptom } = this.props;
        let productObject = symptom.find(symp => parseInt(symp.id) === parseInt(id));
        this.props.manageSymptomAction.updateSymptomFinally(productObject)
    }

    componentDidMount() {
        this.props.manageSymptomAction.getAllSymptoms();
    }
    getSymptoms = () => {
        let allSymptoms = [];
        const { symptom } = this.props;
        symptom.map((symptomObject, index) => {
            // console.log(product, "full product");
            return allSymptoms.push({
                serial: index + 1,
                symptomName: symptomObject.symptomsName,
                description:symptomObject.description,
                dateTimeCreated: moment(symptomObject.dateTimeCreated).format("lll"),
                check: (
                    <div
                        className="custom-control custom-checkbox mb-1"
                        onClick={() => this.toggleCheckBox(symptomObject.id)} >
                        <input
                            type="checkbox"
                            className="custom-control-input teamgrace"
                            id={symptomObject.id}
                            checked={false} onChange={() => this.doNothing()}
                        />
                        <label className="custom-control-label" htmlFor="category2"></label>
                    </div>
                    // <CheckBox type={'checkbox'} checked={this.state.item === symptomObject.id} label={''} onClick={() => this.toggleCheckBox(symptomObject.id)} />
                )

            });
        });
        const data = {
            columns: [
                {
                    label: "S/N",
                    field: "serial",
                    sort: "asc"
                },
                {
                    label: 'Check',
                    field: 'check',
                    sort: 'disabled',
                },
                {
                    label: "Symptom Name",
                    field: "symptomName"
                },
                {
                    label: "Description",
                    field: "description"
                },
                {
                    label: "Date|Time|Created",
                    field: "dateTimeCreated"
                }
            ],
            rows: allSymptoms
        };
        return data;
    }


    render() {
        const { symptom } = this.props
        console.log(symptom)
        return (
            <div className="container-fluid mt-4 mb-3">
                <div className="row">
                    <div className="col-md-12 col-sm-12 " >
                        <div className="row">
                            <div className="form-group col-md-2 col-sm-2">
                                <select id="actionDropDown" className="form-control">
                                    <option value="" defaultValue>Choose...</option>
                                    <option value="edit">Edit</option>
                                    <option value="delete">Delete</option>
                                </select>
                            </div>
                            <div className="form-group col-md-2 col-sm-2">

                                <button
                                    type="button"
                                    onClick={() => this.checkActionToPerform()}
                                    className="btn text-white "
                                    style={{ backgroundColor: "#1B2CC1", height: "35px", borderRadius: '5px' }}

                                >
                                    Apply
                                </button>

                            </div>


                        </div>

                        <DatatablePage
                            data={this.getSymptoms()}
                            searching={true} />
                    </div>

                </div>

                <EditSymptomModal
                />

            </div>
        )
    }
}




const mapStateToProps = (state) => ({
    isLoading: state.LoaderReducer.isLoading,

    symptom: state.ManagerSymptomReducer.symptom

});
const mapDispatchToProps = (dispatch) => ({
    manageSymptomAction: bindActionCreators(ManagerSymptomAction, dispatch)

});

export default connect(mapStateToProps, mapDispatchToProps)(ManagerSymptomTable);




