
import { SHOW_CREATED_SICKNESS, ONCHANGE_HANDLE, START_LOADING, STOP_LOADING, DESCRIPTION_INPUT,UPDATE_SICKNESS } from "./Types";
import{settings,showErrorMessage,showSuccessfulMessage} from "../../../src/components/Utils/Utility"
import axios from "../../components/Utils/Clients";
import store from "../store";

//SHOW_CREATED_SICKNESS table action type.
export const getAllSickness = () => {
    return dispatch => {
        axios.get(settings.baseURL+"sickness")
            .then(response => {
                dispatch({
                    type: SHOW_CREATED_SICKNESS,
                    payload: response.data.data
                })
                console.log(response, " i am response")
            })
            .catch(error => {
                console.log(error, " i am error")
            })
    }
}

//ONCHANGE_HANDLE for input.
export const handleOnchange = (event) => {
    let newvalue = event.target.value;
    return dispatch => {
        dispatch({
            type: ONCHANGE_HANDLE,
            payload: newvalue
        })
    }
}

//sumbitHandle button.
export const submitHandle = (event, input, descriptionValue) => {
    return dispatch => {
        dispatch({ type: START_LOADING })
        event.preventDefault(); //prevent the page from reloading when the button click.
        let payLoad = {
            sicknessName: input,
            sicknessDescription: descriptionValue
        }
        axios.post(settings.baseURL+"sickness",payLoad)
            .then(response => {
                dispatch(getAllSickness())
                console.log(response);
                showSuccessfulMessage("Sickeness created successfully");
                dispatch({type: ONCHANGE_HANDLE,payload: ""
                
            })
            dispatch({ type: STOP_LOADING })
            })
            .catch(error => {
                if(error.response && error.response.status==409){
                    showErrorMessage(error.response.data.message);
                }else{
                    showErrorMessage("Unable to process");
                }
                console.log(error)
                dispatch({ type: STOP_LOADING })
            })
    }
}

//descriptionInput
export const descriptionInput = (event) => {
    let inputvalue = event.target.value;
    console.log(inputvalue)
    return dispatch => {
        dispatch({
            type: DESCRIPTION_INPUT,
            payload: inputvalue
        })
    }
}
export const updateSicknessFinally = (payload) => {
    return dispatch => {
        dispatch({
            type: UPDATE_SICKNESS,
            payload
        })
    }
}
export const updateSickness = (payLoad,responseCallBack) => {
    
    return dispatch => {
        dispatch({ type: START_LOADING })
        axios.put(settings.baseURL + "sickness", payLoad)
            .then(response => {
                showSuccessfulMessage("Sickness updated successfully")
                dispatch(getAllSickness())
                dispatch({ type: STOP_LOADING });
                responseCallBack(true);
            })
            .catch(error => {
                if (error.response && error.response.status === 409) {
                    showErrorMessage(error.response.data.message);
                } else {
                    showErrorMessage("Unable to process");
                }
                console.log({ error }, "i am the post error");
                dispatch({ type: STOP_LOADING })
            })
    }}

    export const deleteSicknessFinally = (responseCallBack) => {
        return (dispatch,getState)=> {
            let payLoad =store.getState().ManagerSicknessReducer.sicknessObject;
            dispatch({ type: START_LOADING })
            axios.delete(settings.baseURL + "sickness/"+payLoad.id)
                .then(response => {
                    dispatch(getAllSickness())
                    dispatch({ type: STOP_LOADING });
                    responseCallBack(true);
                })
                .catch(error => {
                    if (error.response && error.response.status === 409) {
                        showErrorMessage(error.response.data.message);
                    } else {
                        showErrorMessage("Unable to process");
                    }
                    console.log({ error }, "i am the post error");
                    dispatch({ type: STOP_LOADING })
                })
        }

}