import React, { Component } from 'react'
import * as ManagerSymptomAction from "../../Redux/Actions/ManagerSymptomAction";
import * as ManagerSicknessAction from "../../Redux/Actions/MangerSicknessAction";

import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import sicknessImage from "../../assets/images/sickness.png";
import symtpomImage  from  "../../assets/images/symptoms.png"

class SmallStats extends Component {

  componentDidMount() {
    this.props.manageSymptomAction.getAllSymptoms();
    this.props.manageSicknessAction.getAllSickness();
  }


  render() {
    const { symptom, sickness } = this.props;
    return (
      <div className="row" style={{flex:1,justifyContent:"center"}}>
        <div className="col-md-5 col-sm-6 mb-4"  >
          <div className="stats-small stats-small--1 card card-small " style={{flex:1,flexDirection:'row',padding:15,paddingBottom:0,backgroundColor:"rgba(255, 190, 72, 0.3)",borderRadius:20}}>
         
            <div className="card-body p-0 d-flex" >
            
              <div className="d-flex flex-column m-auto">
                <div className="stats-small__data text-center">
                  <span className="stats-small__label text-uppercase" style={{color:"#FFA502",fontSize:24,fontWeight:600,paddingBottom:20}}>Sickness</span>
                  <h6 className="stats-small__value count my-3" style={{fontSize:72,fontWeight:900,color:"#262626"}}>{sickness ? sickness.length : 'Loading...'}</h6>
                </div>

              </div>
              
            </div>
            <div className="card-body p-0 d-flex">
            
            <div className="d-flex flex-column m-auto">
              <div className="stats-small__data"  >
              <img id="main-logo" className="d-inline-block align-top mr-1" style={{ maxWidth: '100px'}}
                  src={sicknessImage}
                />
              </div>

            </div>
          </div>
            </div>
        
        </div>
        <div className="col-md-5 col-sm-6 mb-4" >
          <div className="stats-small stats-small--1 card card-small " style={{flex:1,flexDirection:'row',padding:15,paddingBottom:0,backgroundColor:"rgba(3, 204, 170, 0.3)",borderRadius:20}}>
         
            <div className="card-body p-0 d-flex" >
            
              <div className="d-flex flex-column m-auto">
                <div className="stats-small__data text-center">
                  <span className="stats-small__label text-uppercase" style={{color:"#059179",fontSize:24,fontWeight:600,paddingBottom:20}}>symptoms</span>
                  <h6 className="stats-small__value count my-3" style={{fontSize:72,fontWeight:900,color:"#262626"}}>{symptom ? symptom.length : 'Loading...'}</h6>
                </div>

              </div>
              
            </div>
            <div className="card-body p-0 d-flex">
            
            <div className="d-flex flex-column m-auto">
              <div className="stats-small__data"  >
              <img id="main-logo" className="d-inline-block align-top mr-1" style={{ maxWidth: '130px'}}
                  src={symtpomImage}
                />
              </div>

            </div>
          </div>
            </div>
        
        </div>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  symptom: state.ManagerSymptomReducer.symptom,
  sickness: state.ManagerSicknessReducer.sickness
})
const mapDispatchToProps = (dispatch) => ({
  manageSymptomAction: bindActionCreators(ManagerSymptomAction, dispatch),
  manageSicknessAction: bindActionCreators(ManagerSicknessAction, dispatch)

});
export default connect(mapStateToProps, mapDispatchToProps)(SmallStats);
