import { LOGIN_USER, START_LOADING, STOP_LOADING } from "./Types";
import axios from "../../components/Utils/Clients";
import { settings, showErrorMessage, showSuccessfulMessage,roles} from "../../components/Utils/Utility"
import { history } from "../../components/Utils/History"
import { saveLoggedInUserToLocalStorage, removeFromLocalStorage } from "../../components/Utils/Helpler"
import { Route, Redirect } from 'react-router-dom';


export const loginUser = (payLoad, props) => {
    return dispatch => {

        let config = { headers: { 'Content-Type': ' application/x-www-form-urlencoded' } }
        const formData = new FormData();
        formData.append("username", payLoad.userName);
        formData.append("password", payLoad.password);
        dispatch({ type: START_LOADING })
        axios.post(settings.baseURL + "authentication", formData, config)
            .then(response => {
                dispatch({ type: STOP_LOADING })
                if (response.data.data.role !== roles.ADMIN) {
                    showErrorMessage("Permission not allowed!");
                    return;
                }
                saveLoggedInUserToLocalStorage("user", response.data.data);
                const { from } = props.location.state || { from: { pathname: "/" } };
                dispatch({ type: LOGIN_USER, payLoad: response.data.data });
                props.history.push(from);
            })
            .catch(error => {
                dispatch({ type: STOP_LOADING })
                console.log(" login error", { error });
                if (error.response && error.response.status === 401) {
                    showErrorMessage(error.response.data.message);
                } else {
                    showErrorMessage("Unable to login");
                }
            })
    }
}

export const logUserOut = () => {
    localStorage.clear();
    window.location.href = '/';
    console.log("<<<<<<<<<User logged out successfully>>>>>>>")
}

