import { SHOW_CREATED_SICKNESS, ONCHANGE_HANDLE, DESCRIPTION_INPUT,UPDATE_SICKNESS } from "../Actions/Types";

const initialState = {
    sickness: [],
    input: "",
    descriptionValue: "",
    sicknessObject:null
}

const ManagerSicknessReducer = (state = initialState, action) => {
    switch (action.type) {
        case SHOW_CREATED_SICKNESS:
            return { ...state, sickness: action.payload }

        case ONCHANGE_HANDLE:
            return { ...state, input: action.payload }

        case DESCRIPTION_INPUT:
            return { ...state, descriptionValue: action.payload }
            case UPDATE_SICKNESS:
                console.log("=======sick",action.payload);
                return { ...state, sicknessObject: action.payload }
    

        default:
            return state;
    }
}

export default ManagerSicknessReducer;